.button {
    display: flex;
    align-items: center;
    justify-content: center;

    width: 100%;
    max-width: 280px;
    padding: 10px;
    border-radius: 6px;
    border: none;
    min-height: 59px;

    background-color: $pink;
    color: $white;

    text-decoration: none;
    transition: opacity 0.2s;

    font-size: 16px;
    line-height: 19px;
    font-family: inherit;

    &--blue {
        background-color: $dark-blue;
    }

  &--transparent {
    background-color: transparent;
    color: #898989;
    border: 1px solid #898989;
  }

  &:hover {
    opacity: 0.8;
  }
}

